@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:ital,wght@0,700;1,300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;400;600;800&&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap);
body {
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif; */
  font-family: 'Nunito Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --blue: #83A7D6;
  --white: #F9F8F8;
  --black: #616161;
}

html {
  scroll-behavior: smooth;
}
a{
  -webkit-text-decoration-color: #83A7D6;
          text-decoration-color: #83A7D6;
  -webkit-text-decoration-color: var(--blue);
          text-decoration-color: var(--blue);
  color: #83A7D6;
  color: var(--blue);
}

.App {
  text-align: center;
}

#header{
  display: block;
}

body{
  font-family: 'Poppins', sans-serif;
  background-color: #F9F8F8;
  background-color: var(--white);
  color:#616161;
  color:var(--black);
  font-size: 17px;
}

#topbar{
  display: inline-flex;
  top:0px;
  -webkit-animation: topbardrop ease 1.5s;
          animation: topbardrop ease 1.5s;
  -webkit-transition: top 0.3s;
  transition: top 0.3s;
  z-index: 1;
  height: 60px;
  position: fixed;
  background-color: #F9F8F8;
  background-color: var(--white);
  width: 100%;
}

#navbar{
  display: flex;
  visibility: visible;
  flex-direction: row;
  justify-content: flex-end;
  margin-top:20px;
  margin-right: 20px;
  width:100%
}

#icon{
  margin:20px;
}


@-webkit-keyframes topbardrop{
  0% {
    top:-60px;
  }
  50%{
    top:-60px;
  }
  100% {
    top:0px;
  }
}


@keyframes topbardrop{
  0% {
    top:-60px;
  }
  50%{
    top:-60px;
  }
  100% {
    top:0px;
  }
}


.detail_btn{
  color: #616161;
  color: var(--black);
  padding: 5px 10px 25px 10px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  margin: 4px 10px;
  -webkit-transition-duration: 0.4s;
          transition-duration: 0.4s;
  float:right;
  font-size: 14px;
  font-weight:600;
  padding-bottom: 0px;
  height:25px;
}

.detail_btn:hover {
  color: #83A7D6;
  color: var(--blue);
}
.small-button{
  border: 2px solid #83A7D6;
  border: 2px solid var(--blue);
  border-radius: 5px;
  color: #83A7D6;
  color: var(--blue);
  padding: 5px;
  padding-bottom: 2px;
}

.large-button{
  border: 2px solid #83A7D6;
  border: 2px solid var(--blue);
  border-radius: 5px;
  color: #83A7D6;
  color: var(--blue);
  font-size: 20px;
  padding: 10px;
  margin-left: 20px;
  text-decoration: none;
  text-align: center;
  width:30%;

}
.small-button:hover, .large-button:hover{
  background-color: #DEE8F5;
}


#home{
  display: block;
  padding:10%;
  padding-left: 200px;
  margin-top: 40px;
  margin-bottom: 40px;
}
h1{
  font-family: 'Poppins', sans-serif;
  font-size: 50px;
  line-height: 90%;
  color: #83A7D6;
  color: var(--blue);
  opacity: 50%;
}
#heading_bold{
  opacity: 100%;
}


.heading{
  color:#616161;
  color:var(--black);
  -webkit-transition: all 0.8s ease;
  transition: all 0.8s ease;
  opacity: 0;
}
.heading.active{
  opacity: 1;
}

#Footer{
  font-weight: 300;
  font-size: 15px;
  font-style: italic;
  text-align: center;
  color:gray;
}

#icon{
  justify-content: left;
  height:40px;
}


#About .card{
  display: flex;
  flex-direction: row;
  align-items:flex-start;

  width:80%;
}

#Project-List{
  display: flex;
  justify-content: space-between;
}

.content{
  margin-left: 15%;
  margin-right: 15%;
  margin-top: 10%;
  margin-bottom: 20%;
}

.heading h2{
  display:inline-block;
  margin: 20px;
  margin-left:0px;
  font-weight: 700;
  color:var(--black);
  font-size: 30px;
}
.heading h4{
  display:inline-block;
  margin: 20px;
  font-weight: 300;
  color:var(--black);
  font-style: italic;
}
h3{
  font-weight: 700;
  color: var(--black);
  margin-left: 30%;
}


.card{
  margin-top: 100px;
  margin-bottom: 100px;
  opacity: 0;
  -webkit-transition: all 0.8s ease;
  transition: all 0.8s ease;
  color:black;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: center;
  justify-content: space-around;

}
.card.active{
  opacity: 1;
}

.card_ver1{
  flex-direction: row-reverse;

}

.card_ver2{
  flex-direction: row;
}

.card-heading{
  display: flex;
  flex-direction: row;
  align-items: center;
}

.card-title{
  display: inline-block;
  font-size: 25px;
  font-family: 'Poppins', sans-serif;
  color:var(--blue);
}

.card-content{
  display: flex;
  flex-direction: column;
  align-items: left;
  max-width: 400px;
}
.card-body{
  padding-left: 10%;
  line-height: 130%;
  font-size: 16px;
  font-family: 'Open Sans', sans-serif;
}
.card-tags{
  display: flex;
  flex-direction:row;
  flex-wrap: wrap;
}
.card-tag{
  font-size: 12px;
  font-family: 'Poppins', sans-serif;
  color:#759dd1;
  font-weight:600;
  border: solid;
  border-radius: 10%;
  border-width: thin;
  margin-right:10px;
  padding:2px 5px 2px 5px;
  margin-bottom: 5px;
}

.arrow-demo-link{
  display: inline-block;
  margin-left: 20px;
  width: 20px;
  height:20px;
}

.arrow-demo-link{
  display: inline-block;
  -webkit-transform: scaleX(-1);
          transform: scaleX(-1);
}
.demo-link{
  opacity: 1;
}
.demo-link:hover{
  opacity: 1;
  -webkit-transition: opacity 0.3s ease-in;
  transition: opacity 0.3s ease-in;
}

@media screen and (max-width: 1300px){
  #contact-content{
    flex-direction: column;
  }

}

@media screen and (max-width: 700px){
  .card{
    flex-direction: column;
  }

  #navbar{
    visibility: hidden;
  }

  #grid{
    flex-direction: column;
  }

}

#grid{
  display: flex;
  justify-content: space-between;
}

.grid-card{
  flex:1 1;
  margin:5%;
}
.grid-card .card-body{
  padding-left: 0;
}
.grid-card .card-title{
  margin-bottom: 0px;
  margin-top: 0px;
  font-size: 20px;
}
.grid-card .card-body{
  font-size: 14px;
}

#Contact{
  padding: 20px;
}
#contact-content{
  display: flex;
  flex-direction: row;
  margin-top:30px;
}

#contact-left, #contact-right{
  flex: 1 1;
}
#contact-left{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
#contact-left ul{
  display: inline-block;
  font-family: 'Poppins', sans-serif;
  font-weight:bold;
  color:var(--blue);
}
#contact-left a{
  display: inline-block;
  font-family: 'Poppins', sans-serif;
  font-weight: 400;
  font-size: 15px;
  padding-left: 30px;
  color:var(--black);
}

#contact-right{
  font-size: 15px;
  width: 60%;
}

@media screen and (max-width: 650px){
  #contact-content{
    flex-direction: column;
  }
  #contact-right{
    font-size: 15px;
    width:100%;
    margin-top:30px;
  }
}

#Footer{
  font-weight: 400;
  font-style: normal;
  font-size: 13px;
}

